.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.tooltip-hov{
  width:280px;
  height:60px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin:auto;
  padding:30px 0;
  position:absolute;
  left:40%;
  top:40%;
  border-radius: 20px;
  outline:none;
  font-weight: bold;
  font-family: sans-serif;
  border:1px solid #111;
  transition:1s ease-in-out;
  cursor:pointer;
  background: #0c273d;
  color:#04503f;
  font-size:30px;
  letter-spacing: 3px;
  box-shadow: 1px 2px 13px #062742;
}
tooltip-hov div{
  margin:5px;
}
.tip-position{
  display: flex;
  justify-content: center;
  align-items: center;
  padding:30px 0;

}
.tip-position button{
  margin:10px;
  color:#0a1a27;
  background-color: rgb(21, 100, 100);
  font-size:50px;
  letter-spacing: 5px;
  box-shadow: 1px 2px 13px #274157;
}
.tooltip-hov .left{
  width: 300px;
  background-color: rgb(43, 172, 71);
  color: #a06076;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  
  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: -5px;
  right: 105%;
}
.tooltip-hov .left::after {
  content: " ";
  position: absolute;
  top: 50%;
  left: 100%; /* To the right of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent black;
}
.tooltip-hov .right{
  width: 300px;
  background-color: rgb(43, 172, 71);
  color: #a06076;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  
  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: -5px;
  left: 105%;
}
.tooltip-hov .right::after {
  content: " ";
  position: absolute;
  top: 50%;
  right: 100%; /* To the left of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent black transparent transparent;
}
.tooltip-hov .top{
  width: 300px;
  background-color: rgb(43, 172, 71);
  color: #a06076;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  
  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  margin-bottom: 10px;
  margin-left: -60px;
}
.tooltip-hov .top::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}
.tooltip-hov .bottom{
  width: 300px;
  background-color: rgb(43, 172, 71);
  color: #a06076;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  
  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: 100%;
  left: 50%;
  margin-top: 10px;
  margin-left: -60px;
}
.tooltip-hov .bottom::after {
  content: " ";
  position: absolute;
  bottom: 100%;  /* At the top of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent black transparent;
}